:root {
   --cor_01: #1a2730;
   --cor_02: #45586c;
   --cor_03: #424048;
   --cor_04: #b0cee2;
   --cor_05: #a63e1b;
   --cor_06: #e95d2c;
   --cor_07: #f8cc53;
   --cor_08: #e7ad0c;
   --cor_09: #fff;
   --cor_10: #000;
}
   

/* === Cabeçalho === */
header {
    width: 100%;
    height: 12vh;
    background-color: var(--cor_01);
    color: var(--cor_09);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.header {
    width: 80%;
    height: 12vh;
    

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-img {
    width: auto;
    height: 11vh;
}

@media (max-width: 768px) {
    
    header {
        height: 9vh;
    }
    .header-img {
        height: 7vh;
    }
    
}

@keyframes img {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

/* === Rodapé === */
footer {
    width: 100%;
    height: 12vh;
    background-color: var(--cor_01);
    color: var(--cor_09);

    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.footer {
    width: 80%;
    height: 12vh;
    

    display: flex;
    justify-content: space-between;
    align-items: center;
}
